import React, {Component} from "react";
import {PropTypes} from "prop-types";
import {connect} from "react-redux";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {
    getEntryForLowerInstallmentsModal,
    getLowerInstallmentsBundleNo,
    isLowerInstallmentsModalVisible
} from "eshop/modules/cart/selectors";
import {lowerInstallmentsModaClose} from "eshop/modules/cart/actions/resources";
import SliderComponent from "./SliderComponent";
import {postLowerInstallments} from "../actions/cart";

class LowerInstallmentsModal extends Component {

    constructor(props) {
        super(props);
    }

    renderInfo() {
        return (
            <div>
                {!!this.props.descriptions && this.props.descriptions.switchToNetModalMsg || "Aby obniżyć ratę zostaniesz przekierowany na zakładkę netto."}
            </div>
        )
    }

    render() {
        return (
            <OraModal id="LowerInstallmentsModal"
                      key="LowerInstallmentsModal"
                      open={this.props.open}
                      onClose={this.props.onClose.bind(this, this.props.bundleNo)}
                      size="narrow"
                      showClose={true}>
                <SliderComponent entry={this.props.entry}
                                 bundleNo={this.props.bundleNo}
                                 postLowerInstallments={this.props.postLowerInstallments}
                                 showNetPrices={this.props.showNetPrices}
                                 cartIsNet={this.props.cartIsNet}
                                 descriptions={this.props.descriptions}
                                 open={this.props.open}
                                 key={"SLIDER"}/>
            </OraModal>
        );
    }
}

const mapStateToProps = (state) => ({
    open: isLowerInstallmentsModalVisible(state),
    bundleNo: getLowerInstallmentsBundleNo(state),
    entry: getEntryForLowerInstallmentsModal(state)
});

const mapDispatchToProps = (dispatch) => ({
    onClose: (bundleNo) => dispatch(lowerInstallmentsModaClose(bundleNo)),
    postLowerInstallments: (bundleNo, diff, market) => dispatch(postLowerInstallments(bundleNo, diff, market))
})

export default connect(mapStateToProps, mapDispatchToProps)(LowerInstallmentsModal);
